import { ChangeDetectorRef, Component, OnInit,ViewChild } from '@angular/core';
import { Validators, FormBuilder, FormGroup,FormControl,ValidatorFn,AbstractControl } from '@angular/forms';
import { MasterService } from 'app/shared/services/master/master.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { DatePipe } from '@angular/common';
import {CityService} from 'app/shared/services/geographics/city.service';
import{StateService} from 'app/shared/services/geographics/state.service';
import {CountryService} from 'app/shared/services/geographics/country.service';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {MessageConstant} from 'app/shared/utils/message.constant';
import {MatChipInputEvent} from '@angular/material/chips';
import {COMMA, ENTER} from '@angular/cdk/keycodes';
import * as XLSX from 'xlsx';
import { MatTableDataSource } from '@angular/material/table';
import { ExcelService} from 'app/shared/services/excel.service'
import { MatPaginator } from '@angular/material/paginator';
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
} 
export interface PIN {
  PIN: string;
}
export class PinData {
  //SERIAL_No: string;
  PIN: string = "";
  remarks:string = "";
  }

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations,
  providers:[DatePipe]
})
export class CreateComponent implements OnInit {
  form: FormGroup;
  addOnBlur = true;
  readonly separatorKeysCodes = [ENTER, COMMA] as const;
  pins: PIN[] = [];
  preSelectedPins:PIN[] = [];
  localStorage : any;
  @ViewChild("toggleElement") ref: any;
  status = false;
  states :any;
  filteredOptionState: Observable<any[]>;
  countries :any;
  filteredOptionCountry: Observable<any[]>;
  countrty_id:any;
  errorMsg:String;
  splitted:String;
  mappedColumns: string[] = ['PIN','remarks'];
mappedDataSource :MatTableDataSource<PinData>;
unmappedColumns: string[] = ['PIN','remarks'];
returnDataStatus :boolean = false;
@ViewChild('mappedPaginator') mappedPaginator: MatPaginator;
@ViewChild('unmappedPaginator') unmappedPaginator: MatPaginator;
response_mapped =[];
  response_unmapped = [];
unmappedDataSource :MatTableDataSource<PinData>;
  constructor(private fb: FormBuilder,
    private snackBar: MatSnackBar,private cdr :ChangeDetectorRef,
    private router : Router,public stateService: StateService,public countryService:CountryService) {
     
    
  }

  ngOnInit() {
    this.initialize();
    // this.getState();
   this.getCountry();
   
  }

  initialize(){

    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));

    this.form = this.fb.group({
     // name:['',[Validators.required,Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
     
      state :['',[autocompleteObjectValidator(),Validators.required]],
      country :['',[autocompleteObjectValidator(),Validators.required]],
      status : this.status == true ? 'Y' : 'N'
     });
  
   
 
  }
 
  onSubmit(formDirective){
    if (this.form.invalid){
      return;
    }else{
      if(this.pins.length<=0){
        this.snackBar.open(MessageConstant.pinError, 'Dismiss', { duration: 5000 });
      }
      else{
        let formValues = this.form.value;
        ////
    
        
        let params = {
          //name :formValues.name, 
          PIN_data: this.pins.map(v => ( v.PIN)),
          state_id:formValues.state.id,
          status :  this.status == true ? 'Y' : 'N', 
          created_by : this.localStorage.userId 
        }
    
        
        this.stateService.createStatePINMapping(params).subscribe(
          (resp:any )=>{
           if(resp.status == true){
            this.snackBar.open('State Pin mapping successfully!', 'Dismiss', { duration: 5000 });
            //this.router.navigate(['/manageGeographics/manageStatePin/list']);
            this.response_mapped = resp.data[0].PIN_mapped;
            this.response_unmapped = resp.data[0].PIN_not_mapped;
            formDirective.resetForm();
            this.form.reset();
            this.pins=[];
            this.returnDataStatus = true;
            this.getCountry();
            this.form.markAsPristine();
            if (this.response_mapped.length<0){
            }else{
                 this.cdr.detectChanges();
                  this.mappedDataSource = new MatTableDataSource(this.response_mapped);
                this.mappedDataSource.paginator = this.mappedPaginator;
            }
            if (this.response_unmapped.length<0){
            }else{
              this.cdr.detectChanges();
                  this.unmappedDataSource = new MatTableDataSource(this.response_unmapped);
                  this.unmappedDataSource.paginator = this.unmappedPaginator;
            }
            this.snackBar.open(MessageConstant.statePinMapped, 'Dismiss', { duration: 5000 });
           } else{
             
            if(resp.err_code == "ERROR : ER_DUP_ENTRY"){
              this. errorMsg = resp.message;
              var index=  this. errorMsg.lastIndexOf(" ");
             
              this.splitted = this. errorMsg.substring(index+1); 
              if(this.splitted == "'state_id'" ){
                this.snackBar.open(MessageConstant.nameDuplicateCity, 'Dismiss', { duration: 5000 });
              }
             
            }else{
              this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
            }
           // this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
           }
          
    
          },
          (err)=>{
            this.snackBar.open('Please try again', 'Dismiss', { duration: 5000 });
          }
        )
      }
      
    }
   

  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }
  updateStatus(event){
    this.status = event.checked;
    this.ref.checked = this.status;
  }
  // getState(){
  //   this.stateService.getAllState().subscribe(
  //     (data:any)=>{
  //       this.states = data;
  //        this.filteredOptionState = this.form.controls.state.valueChanges
  //          .pipe(
  //            startWith(''),
  //            map(value => typeof value === 'string' ? value : value.name),
  //            map(name => name ? this._filterState(name) : this.states)
  //          );
  //        },(err)=>{
           
  //        }
  //   )
  // }
  displayState(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterState(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.states.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  getStatesByCountry(country){
    this.countrty_id = country.id;
    this.stateService.getStateByCountry(this.countrty_id).subscribe(
      (stateData:any)=>{
        this.states = stateData;
         this.filteredOptionState = this.form.controls.state.valueChanges
           .pipe(
             startWith(''),
             map(value => typeof value === 'string' ? value : value.name),
             map(name => name ? this._filterState(name) : this.states)
           );
         },(err)=>{
           
         }
    )
  }
  
  onCountryChange(event){
   this.getStatesByCountry(event);
   this.states=[];
   this.form.get('state').setValue( "");
  }
  getCountry(){
    this.countryService.getAllCountry().subscribe(
      (data:any)=>{
        this.countries = data;
         this.filteredOptionCountry = this.form.controls.country.valueChanges
           .pipe(
             startWith(''),
             map(value => typeof value === 'string' ? value : value.name),
             map(name => name ? this._filterCountry(name) : this.countries)
           );
         },(err)=>{
           
         }
    )
  }
  displayCountry(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCountry(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.countries.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    // Add our fruit
    if ((value || '').trim()) {
      const toSelect = this.preSelectedPins.find(c => c.PIN == value.trim());
      if(toSelect == null || toSelect == undefined){
        this.pins.push({PIN: value.trim()});
      }else{
        
      }
      
    }

    // Reset the input value
    if (input) {
      input.value = '';
    }
  }

  remove(pin: PIN): void {
    const index = this.pins.indexOf(pin);

    if (index >= 0) {
      this.pins.splice(index, 1);
    }
  }
  onStateChange(state){
    this.stateService.allActivePINByStateId(state.id).subscribe(
      (stateData:any)=>{
          this.preSelectedPins = stateData.data;
         },(err)=>{
           
         }
    )
  }
}


