import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { CityService } from 'app/shared/services/geographics/city.service';
import { StateService } from 'app/shared/services/geographics/state.service';
import { DatePipe } from '@angular/common';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { CountryService } from 'app/shared/services/geographics/country.service';
import { MatChipInputEvent } from '@angular/material/chips';
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}

export interface PIN {
  PIN: string;
}

@Component({
  selector: 'app-view-dialog',
  templateUrl: './view-dialog.component.html',
  styleUrls: ['./view-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations,
  providers: [DatePipe]
})
export class ViewDialogComponent implements OnInit {

 
  params: any;
  isEditMode = false;
  form: FormGroup;
  localStorage: any;
  status = false;
  addOnBlur = true;
  readonly separatorKeysCodes = [] as const;

  @ViewChild("toggleElement") ref: any;
  states: any;
  countrty_id: any;
  pins: PIN[] = [];
  filteredOptionState: Observable<any[]>;
  countries: any;
  filteredOptionCountry: Observable<any[]>;
  constructor(private fb: FormBuilder, private cityService: CityService, private stateService: StateService, private snackBar: MatSnackBar, private datePipe: DatePipe,
    public dialogRef: MatDialogRef<ViewDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: MtxDialogData, private countryService: CountryService) { }

  ngOnInit() {
    this.initialize();
    this.getCountry();
  }

  initialize() {

    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));

    this.params = this.data;

    this.isEditMode = this.params.action == 'CREATE' ? false : true;

    this.form = this.fb.group({
      // name:['',[Validators.required,Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],

      state: ['', [autocompleteObjectValidator(), Validators.required]],
      country: ['', [autocompleteObjectValidator(), Validators.required]],
      status: this.status == true ? 'Y' : 'N'
    });

    if (this.isEditMode) {
      this.assignValues(this.params);
    }
  }

  close() {

    this.dialogRef.close();
  }
  closeActionPanel() {
    this.dialogRef.close();
  }

  onYesClick() {
    let formValues = this.form.value;

    const params: any = {

      PIN_data: this.pins.map(v => ( v.PIN)),
      state_id:formValues.state.id,
      status :  this.status == true ? 'Y' : 'N', 
      created_by : this.localStorage.userId 
    }
    
    if (this.isEditMode) {
      params.id = this.params.data.id,
        params.modify_by = this.localStorage.userId;
    } else {
      params.created_by = this.localStorage.userId;
    }

    // if (this.isEditMode) {
    //   this.cityService.update(params).subscribe(
    //     (cityData: any) => {
    //       if (cityData.status == true) {
    //         this.snackBar.open('Record updated successfully!', 'Dismiss', { duration: 5000 });
    //         this.dialogRef.close();
    //       } else {
    //         this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
    //         this.dialogRef.close();
    //       }

    //     },
    //     (err) => {
    //       this.snackBar.open('Please try again', 'Dismiss', { duration: 5000 });
    //     }
    //   )
    // } else {
    //   this.cityService.create(params).subscribe(
    //     (data: any) => {
    //       this.snackBar.open('Rfid created!', 'Dismiss', { duration: 5000 });
    //       this.dialogRef.close();
    //     },
    //     (err) => {
    //       this.snackBar.open('Please try again', 'Dismiss', { duration: 5000 });
    //     }
    //   )

    // }

    this.dialogRef.close(params);
  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }

  assignValues(params) {
    let sta_id = params.data.state_id;
    this.form.get('country').setValue(params.data.name);
    this.form.get('state').setValue(params.data.state_name);
    //this.form.get('pi_n').setValue(params.data.PIN);
    this.setDefaultPin(sta_id);

    this.status = params.data.status == 'Y' ? true : false;
  }
  setDefaultPin(id){
    this.stateService.allActivePINByStateId(id).subscribe(
      (stateData: any) => {
        this.pins = stateData.data;
      }, (err) => {

      }
    )
  }

  updateStatus(event) {
    this.status = event.checked;
    this.ref.checked = this.status;
  }


  getCountry(){
    this.countryService.getAllCountry().subscribe(
      (data:any)=>{
        this.countries = data;
        if(this.isEditMode){
          //;
          const toSelect = this.countries.find(c => c.id == this.params.data.country_id);
          this.form.get('country').setValue(toSelect);
          this.getStatesByCountry(toSelect);
          this.filteredOptionCountry = this.form.controls.country.valueChanges
           .pipe(
             startWith(''),
             map(value => typeof value === 'string' ? value : value.name),
             map(name => name ? this._filterCountry(name) : this.countries)
           );
        }
         
         },(err)=>{
           
         }
    )
  }
  displayCountry(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCountry(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.countries.filter(option => option.name.toLowerCase().includes(filterValue));
  }

  onCountryChange(event) {
    this.getStatesByCountry(event);
    this.states = [];
    this.form.get('state').setValue("");
  }
  getStatesByCountry(country) {
    this.countrty_id = country.id;
    this.stateService.getStateByCountry(this.countrty_id).subscribe(
      (stateData: any) => {
        this.states = stateData;
        if(this.isEditMode){
          const toSelect = this.states.find(c => c.id == this.params.data.state_id);
          this.form.get('state').setValue(toSelect);
          this.filteredOptionState = this.form.controls.state.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterState(name) : this.states)
          );
        }
        
      }, (err) => {

      }
    )
  }
  displayState(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterState(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.states.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  onStateChange(state) {
    //console.log(state);
    this.stateService.allActivePINByStateId(state.id).subscribe(
      (stateData: any) => {
       // console.log(stateData);
        this.pins = stateData.data;
      }, (err) => {

      }
    )
  }
  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    // Add our fruit
    if ((value || '').trim()) {
      this.pins.push({ PIN: value.trim() });
    }

    // Reset the input value
    if (input) {
      input.value = '';
    }
  }

  remove(pin: PIN): void {
    const index = this.pins.indexOf(pin);

    if (index >= 0) {
     // this.pins.splice(index, 1);
    }
  }
}





